import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DirectorySetupComponent } from './components/directory-setup/directory-setup.component';
import { MaintainDirectoryComponent } from './components/maintain-directory/maintain-directory.component';


const routes: Routes = [
  { path: '', redirectTo: 'setup', pathMatch: 'full'},
  { path: 'setup', component: DirectorySetupComponent},
  { path: 'maintain', component: MaintainDirectoryComponent}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DirectoryRoutingModule { }
